import { Component, h, Host, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-summary-container',
  styleUrl: "summaryContainer.css"
})
export class OrderSummaryContainer {
    @Prop() heading: string = "";
    @Prop() returnHeading: string = "";
    @Prop() returnLink: string = "";

    render() {
        return <Host>
            <h1>{this.heading}</h1>

            <div class="info">
                <slot />
            </div>

            <ks-button tall rounded link={this.returnLink} name={this.returnHeading} />
        </Host>;
    }
}