ks-order-summary-container {
    display: block;
    padding: 30px;
    padding-top: 40px;
    text-align: center;
}

ks-order-summary-container h1 {
    font-weight: 700;
    font-size: 24px;
    margin-bottom: 30px;
}

ks-order-summary-container .info {
    max-width: 700px;
    margin: auto;
    margin-bottom: 40px;
}

ks-order-summary-container > ks-button > * {
    font-size: 18px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}

@media (max-width: 720px) {
    ks-order-summary-container {
        padding: 15px;
        padding-top: 40px;
    }
}